import { IsString } from 'class-validator';

export class CreateModelGroup {
    @IsString()
    mn_distribution_comment: string
    @IsString()
    mn_status: string
    @IsString()
    mn_distribution_date: string
    @IsString()
    mn_source_system: string
    @IsString()
    mn_group: string
    @IsString()
    mn_initial_assignment_date: string
}